@import url('https://fonts.googleapis.com/css?family=Fjalla+One');

#main-logo-container {
  display: block;
  overflow: hidden;

  margin-left: 20px;
  margin-right: 55px;
}

#main-logo {
  font-family: 'Fjalla One', sans-serif;
  color: white;

  font-size: 30px;
  text-decoration: none;
}

#navigation-bar {
  background-color: black;
}

#navigation-tabs {
  display: block;
  overflow: hidden;
}

#social-buttons {
  display: block;
  overflow: hidden;

  position: absolute;
  right: 0;

  margin-right: 30px;
}

#social-buttons > a {
  margin: 0 5px;
}

.social-icon {
  width: 30px;
}



